.country {
    stroke-width: 1;
    stroke: #2f4858;
    fill: #008c86;
  }

  .selected-country {
    stroke-width: 1;
    stroke: #bc5b40;
    fill: #f88f70;
  }

  .affected-marker {
    stroke-width: 1;
    stroke: #bc5b40;
    fill: #f88f70;
    fill-opacity: 0.7;
  }
  div.tooltip {
    position: absolute;
    text-align: center;
    width: 60px;
    height: 28px;
    padding: 2px;
    font: 12px sans-serif;
    background: #f7f2cb;
    border: 0px;
    border-radius: 8px;
    pointer-events: none;
  }
